@import url(https://fonts.googleapis.com/css2?family=Aldrich&family=Sarpanch:wght@900&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

/*
 * Fonts
 */

/* default settings */
body {
  font-family: 'Aldrich', sans-serif;
  color: white;
	text-align: center;
	background: rgb(12, 37, 122);
}

/*
 * Links and buttons
 */

a, button {
	font-family: inherit;
	color: inherit;
	font-size: inherit;
}
.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 2em;
  font-size: 2vw;
}

h1 {
  color: rgb(163, 163, 163);
  font-size: 8vw;
  margin-bottom: 0.25em;
  font-weight: 700;
  font-family: 'Sarpanch', sans-serif;
}

.hide-restart {
  display: none;
}

#restart-btn {
  border: 1px solid white;
  margin-bottom: 2em;
  padding: 1em 1.5em;
  border-radius: 10px;
  background-color: transparent;
}

#restart-btn:hover {
  background-color: white;
  color: rgb(12, 37, 122);
  cursor: pointer;
}

.board {
  width: 100%;
  position: relative;
}

/* hack to allow board's height to match its width */
.board::after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.board .content {
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-gap: 0.1em;
  gap: 0.1em;
}

.board-element {
  border: 1px solid white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.allowed {
  background-color: green;
  cursor: pointer;
}

.not-allowed {
  cursor: not-allowed;
}

.not-allowed:hover {
  background-color: rgba(224, 5, 5, 0.5);
}

.hit {
  color: red;
}

.board-element.computer {
  cursor: crosshair;
}

.board-element.computer:hover {
  background-color: green;
}

.computer.hit,
.computer.miss {
  cursor: not-allowed;
}

.computer.hit:hover,
.computer.miss:hover {
  background-color: rgba(224, 5, 5, 0.5);
}

.board-element.ship {
  background-color: rgba(128, 128, 128, 0.5);
}

/* tablet display */
@media screen and (max-width: 1023px) {
  .board {
    font-size: 3.5vw;
  }
}

.setup {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 75%;
  height: 100%;
}

.axis-selection {
  border: 1px solid white;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 0.5em 1em;
  border-radius: 5px;
  margin-bottom: 1em;
}

.axis-selection:hover {
  background-color: rgb(255, 255, 255, 0.5);
  cursor: pointer;
}

/* mobile display */
@media screen and (max-width: 768px) {
  .setup {
    width: 100%;
  }
}

.main {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.grid-container {
  -webkit-flex-basis: 40%;
          flex-basis: 40%;
}

.grid-container h2 {
  margin-bottom: 0.5em;
  font-size: 3vw;
}

/* tablet display */
@media (max-width: 1023px) {
  .main {
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 70px;
    gap: 70px;
  }

  .grid-container h2 {
    font-size: 3.5vw;
  }

  #enemy {
    -webkit-order: 1;
            order: 1;
  }

  #friendly {
    -webkit-order: 2;
            order: 2;
  }
}
.dialog-box {
  border: 1px solid white;
  margin-bottom: 4em;
  padding: 1.5em 0;
  border-radius: 10px;
  width: 85%;
}

.dialog-box p {
  -webkit-animation: type 3s steps(60, end);
          animation: type 3s steps(60, end);
  white-space: nowrap;
  overflow: hidden;
  margin: 0 auto;
  letter-spacing: .10em;
}

@-webkit-keyframes type {
  from { width: 0 }
  to { width: 100% }
}

@keyframes type {
  from { width: 0 }
  to { width: 100% }
}

/* mobile display */
@media screen and (max-width: 768px) {
  .dialog-box {
    width: 100%;
  }
}

